.clearfix:after {
  content: '';
  display: table;
  clear: both; }

header {
  padding: 2rem 0;
  position: relative;
  text-align: center; }
  header::before {
    background: #e9d37d;
    border-radius: 100%;
    content: '';
    height: 15rem;
    left: 50%;
    margin-left: -7.5rem;
    position: absolute;
    top: 5rem;
    width: 15rem;
    z-index: 1; }

.app-logo {
  opacity: .9;
  position: relative;
  z-index: 2; }

.app-title {
  color: #282826;
  font-family: "Archivo Black", Helvetica, Arial, Roboto, sans-serif;
  font-size: 2.4rem;
  opacity: .9;
  padding: 1rem;
  position: relative;
  text-align: center;
  text-transform: uppercase;
  z-index: 2; }

.train-chart {
  position: relative; }

.x-axis-header ul {
  margin-left: 6rem; }
  .x-axis-header ul li {
    float: left;
    text-align: left;
    width: calc(100% / 12); }
    .x-axis-header ul li span {
      border-bottom: 0.3rem solid #e9d37d;
      font-family: "Work Sans", Helvetica, Arial, Roboto, sans-serif;
      font-size: .8rem;
      font-weight: 600; }
    .x-axis-header ul li::before {
      border-left: 0.1rem dotted #282826;
      content: '';
      height: 100%;
      position: absolute;
      top: 1.5rem;
      z-index: 3; }

.time-table {
  margin-top: 1rem; }

.date-line {
  padding: 1rem 0;
  position: relative; }
  .date-line::before {
    background: transparent;
    content: '';
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    transition: background .1s linear;
    width: 100%;
    z-index: 2; }
  .date-line:hover::before {
    background: rgba(255, 255, 255, 0.5) !important; }
  .date-line:hover .date-display::after {
    transform: rotate(45deg) scale(1.15); }

.date-line:nth-child(even)::before {
  background: rgba(255, 255, 255, 0.15); }

.month-bar {
  background: #e9d37d;
  height: 100%;
  left: 0;
  position: absolute;
  top: 2.3rem;
  width: 1.5rem;
  z-index: 10; }

.month-display {
  color: #282826;
  font-family: "Work Sans", Helvetica, Arial, Roboto, sans-serif;
  font-size: .8rem;
  font-weight: 700;
  left: -1.5rem;
  position: absolute;
  text-transform: uppercase;
  top: -.5rem;
  transform: rotate(-90deg) translate(-50%, 0);
  z-index: 10; }

.date-display {
  height: 100%;
  left: .1rem;
  overflow: hidden;
  position: absolute;
  text-align: right;
  top: 0;
  width: 6rem;
  z-index: 4;
  /* &::before {
    background: $highlight-color;
    border-radius: 100%;
    content: '';
    height: 6rem;
    right: -3rem;
    position: absolute;
    top: -3rem;
    width: 6rem;
  } */ }
  .date-display span {
    color: #e5e5d9;
    font-family: "Work Sans", Helvetica, Arial, Roboto, sans-serif;
    font-size: 1rem;
    font-weight: 600;
    padding: .4rem .3rem 0 0;
    position: relative;
    z-index: 1; }
  .date-display::after {
    background: #282826;
    content: '';
    height: 4.4rem;
    right: -2.2rem;
    position: absolute;
    top: -2.2rem;
    transform: rotate(45deg) scale(1);
    transition: transform .1s linear;
    width: 4.4rem; }

.entries-for-date {
  align-items: flex-start;
  display: flex;
  margin-left: 6rem;
  position: relative;
  z-index: 4; }

.engine-manifest {
  -webkit-appearance: none;
  /* stylelint-disable-line property-no-vendor-prefix */
  -moz-appearance: none;
  /* stylelint-disable-line property-no-vendor-prefix */
  background-color: transparent;
  border: 0;
  cursor: pointer;
  left: 0;
  position: absolute;
  transform: translateY(0);
  top: 0;
  transition: transform .15s linear;
  z-index: 2; }
  .engine-manifest:hover {
    transform: translateY(-5px); }
  .engine-manifest::after {
    background: #282826;
    border-radius: 1rem;
    clear: both;
    color: white;
    content: '...';
    float: left;
    font-family: "Work Sans", Helvetica, Arial, Roboto, sans-serif;
    font-weight: 500;
    padding: .2rem .8rem .3rem;
    text-align: center;
    transform: scale(0, 0);
    transition: all .15s ease-in-out .1s; }
  .engine-manifest:hover::after {
    transform: scale(1, 1); }
  .engine-manifest .engine-number {
    border-radius: 1rem;
    background: #e5e5d9;
    clear: both;
    color: white;
    float: left;
    font-family: "Work Sans", Helvetica, Arial, Roboto, sans-serif;
    font-weight: 500;
    margin-bottom: .1rem;
    padding: .2rem .8rem;
    position: relative;
    z-index: 1; }

.detail-panel {
  background: white;
  bottom: 0;
  height: 20rem;
  left: 0;
  position: fixed;
  width: 100%;
  z-index: 1000; }
